.turntable {
  position: relative;
  overflow: hidden; }
  .turntable .turntable-wrap {
    transition: transform; }
    .turntable .turntable-wrap .turntable-mask,
    .turntable .turntable-wrap .turntable-main,
    .turntable .turntable-wrap .turntable-item {
      position: absolute;
      left: 0;
      top: 0;
      margin: 0;
      padding: 0; }
    .turntable .turntable-wrap .turntable-item {
      width: 100%;
      height: 100%;
      list-style: none;
      text-align: center;
      box-sizing: border-box;
      padding-top: 30px; }
  .turntable .turntable-action {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    box-shadow: 0 0 5px #aaa;
    color: white;
    background-color: #e44025;
    cursor: pointer;
    font-size: 14px;
    text-align: center;
    line-height: 48px;
    transform: translate(-50%, -50%); }
    .turntable .turntable-action::after {
      position: absolute;
      content: '';
      left: 50%;
      top: -16px;
      color: white;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-bottom: 18px solid #e44025;
      transform: translateX(-50%); }
    .turntable .turntable-action[data-disabled] {
      background-color: #aaa;
      cursor: not-allowed; }
    .turntable .turntable-action[data-disabled]::after {
      border-bottom-color: #aaa;
      cursor: not-allowed; }
